.grid-container{
    display: grid;
    float: left;
    width: 150px;
    height: 190px;
    grid-template-columns:1fr 1fr 1fr;
    grid-template-rows:40px 1fr 1fr 1fr;
}
.grid-container>.grid-title{
    text-align: center;
    justify-self: center;
    -ms-flex-item-align: center;
        align-self: center;
    width: 100%;
    height: 100%;
    grid-column: 1 /4;
}
.grid-container>.grid-box{
    text-align: center;
    border: 1px solid #888;
    justify-self: center;
    -ms-flex-item-align: center;
        align-self: center;
    width: 100%;
    height: 100%;
    background: rgb(208, 217, 241);
}
.grid-info{
    float: left;
    margin-left: 50px;
}
.hide{
    display: none;
}


