.show-input{
    position: relative;;
    left: 0;
    top: 0;
}
.hide-input{
    display: none;
}
p{
    position: relative;
}
textarea{
    border-radius: 8px;
    font-size: 1.2rem;
    text-indent: 0.5rem;
    height: 3rem;
    border: 1px solid #ccc;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
textarea:focus{
    border-color: transparent;
    -webkit-box-shadow: 0px 0px 0px 2px rgb(138, 165, 255);
            box-shadow: 0px 0px 0px 2px rgb(138, 165, 255);
    outline: none;
}



