
.silider-container{
    margin: 0 auto;
    width: 400px;
    height: 500px;
}
.fav-container{
    position: fixed;
    right: 55px;
    top: 75%;
    z-index: 100;
}
.fav-dialog{
    position: absolute; 
    width:250px;
    height: 150px;
    background: rgb(235, 235, 235);
    bottom: 55px;
    border-radius:8px;
    -webkit-box-shadow: 0 6px 30px 0 rgba(0,0,0,.35)!important;
            box-shadow: 0 6px 30px 0 rgba(0,0,0,.35)!important;
    -webkit-transform: scale(0);
        -ms-transform: scale(0);
            transform: scale(0);
    -webkit-transform-origin: 190px bottom;
        -ms-transform-origin: 190px bottom;
            transform-origin: 190px bottom;
    -webkit-transition: all 0.5s ease;
    -o-transition: all 0.5s ease;
    transition: all 0.5s ease;
    opacity: 0;
    text-align: center;
    padding: 8px 8px;
}
.fav-dialog::before{
    content: "";
    display: block;
    position: absolute;
    width:0;
    height: 0;
    border: 8px solid transparent;
    border-top-color: rgb(235, 235, 235);
    left: 192px;
    top: 100%;
    z-index:1;
}
.fav-dialog h3{
    font-family: "微软雅黑","黑体","楷体";
    margin-bottom: 5px;
    font-size: 20px;
}
.fav-dialog p{
    color: rgb(151, 146, 146);
    margin-bottom: 3px;
}
.fav-dialog>.rates{
    overflow: hidden;
}
.fav-dialog>.rates>.rate{
    position: relative;
    display: inline-block;
    width: 50px;
    height: 40px;
    opacity: 0;
    left: 0;
    right: 0;
}
.fav-dialog>.rates i{
    position: absolute;
    font-size: 20px;
    cursor: pointer;
    top: 10px;
    left: 0;
    right: 0;
}
.fav-dialog>.rates i:hover{
    -webkit-transform: scale(1.4);
        -ms-transform: scale(1.4);
            transform: scale(1.4);
}
.fav-dialog>.rates>.rate:first-child>i:hover{
    color: rgb(250, 23, 23);
}
.fav-dialog>.rates>.rate:nth-child(2)>i:hover{
    color: rgb(240, 127, 35);
}
.fav-dialog>.rates>.rate:nth-child(3)>i:hover{
    color: rgb(230, 227, 82);
}
.fav-star{
    text-align: left;
    position: absolute;
    bottom: 0px;
    left: 8px;
}
.fav-toggle{
    -webkit-transform: scale(1);
        -ms-transform: scale(1);
            transform: scale(1);
    opacity: 1;
}
.fav-icon-toggle{
    -webkit-animation: iconAni ease 1.5s infinite;
            animation: iconAni ease 1.5s infinite;
}
@-webkit-keyframes iconAni{
    0%{
        -webkit-transform:scale(1);
                transform:scale(1);
    }
    50%{
        -webkit-transform:scale(1.5);
                transform:scale(1.5)
    }
    100%{
        -webkit-transform:scale(1);
                transform:scale(1)
    }
}
@keyframes iconAni{
    0%{
        -webkit-transform:scale(1);
                transform:scale(1);
    }
    50%{
        -webkit-transform:scale(1.5);
                transform:scale(1.5)
    }
    100%{
        -webkit-transform:scale(1);
                transform:scale(1)
    }
}
.fav-dialog>.rates>.fav-i-in1{
    -webkit-animation: iconIn ease 1s;
            animation: iconIn ease 1s;
    -webkit-animation-fill-mode:forwards;
            animation-fill-mode:forwards;
}
.fav-dialog>.rates>.fav-i-in2{
    -webkit-animation: iconIn ease 1s 0.15s;
            animation: iconIn ease 1s 0.15s;
    -webkit-animation-fill-mode:forwards;
            animation-fill-mode:forwards;
}
.fav-dialog>.rates>.fav-i-in3{
    -webkit-animation: iconIn ease 1s 0.3s;
            animation: iconIn ease 1s 0.3s;
    -webkit-animation-fill-mode:forwards;
            animation-fill-mode:forwards;
}
@-webkit-keyframes iconIn{
    0%{
        top:50px;
        opacity: 0;
    }
    100%{
        top:0px;
        opacity: 1;
    }
}
@keyframes iconIn{
    0%{
        top:50px;
        opacity: 0;
    }
    100%{
        top:0px;
        opacity: 1;
    }
}
.fav-dialog>.rates .i-opacity>i{
    -webkit-animation: iconopacity ease 2s;
            animation: iconopacity ease 2s;
    -webkit-animation-fill-mode:forwards;
            animation-fill-mode:forwards;
}
@-webkit-keyframes iconopacity{
    0%{
        opacity:0;
    }
    100%{
        opacity:1;
    }
}
@keyframes iconopacity{
    0%{
        opacity:0;
    }
    100%{
        opacity:1;
    }
}
.fav-icon{
    border-radius: 50%;
    font-size: 40px;
    margin-left: 180px;
    color: rgb(255, 85, 85);
    cursor: pointer;
    display: inline-block;
    font-style: normal;
    vertical-align: baseline;
    text-align: center;
    text-transform: none;
    line-height: 1;
    text-rendering: optimizeLegibility;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    -webkit-transition: all 0.4s cubic-bezier(.77,2.04,1,1);
    -o-transition: all 0.4s cubic-bezier(.77,2.04,1,1);
    transition: all 0.4s cubic-bezier(.77,2.04,1,1);
}
.fav-icon:after{
    content: "\E6A3";
    display: block;
    font-family: "anticon" !important;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
.Todo-input{
    width: 400px;
}
.Todo-input>*{
    margin: 5px 0px;
}


